form {
  width: 90%;
  max-width: 300px;
  background-color: #fff;
  padding: 33px 40px;
  display: flex;
  align-content: center;
  flex-direction: column;
  border-radius: 10px;
}

label {
  text-align: center;
  font-weight: bold;
  font-size: 22px;
  margin-bottom: 26px;
  color: black;
}

textarea {
  background-color: #F9FBFC;
  border: 2px, solid #202329;
  border-radius: 2px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  color: #1E1E1F;
  font-size: 20px;
  text-align: center;
  padding: 12px;
  width: calc(100%-25px);
  max-width: 220px;
  max-height: 300px;
  min-width: 220px;
  min-height: 100px;
  /* resize: none; */
}

textarea::placeholder {
color: #a5a5a5;
font-family: 'Montserrat';
font-weight: 400;
}

textarea:focus {
  outline-color: #61DAFA;
}

.TodoForm-buttonContainer {
  margin-top: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.TodoForm-button {
  cursor: pointer;
  display: inline-block;
  font-size: 20px;
  color: #202329;
  font-weight: 400;
  width: 120px;
  height: 48px;
  border-radius: 2px;
  border: none;
  font-family: 'Montserrat';
}

.TodoForm-button-add {
  background-color: #61DAFA;
  box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}

.TodoForm-button-cancel {
  background: transparent;
  box-shadow: 1px 1px 20px 0px rgb(97 218 250 / 50%);
}


@media (min-width: 1025px) {
  form {
    max-width: 500px;
    height: 400px;
  }

  textarea {
    max-width: 420px;
    max-height: 200px;
    min-width: 420px;
    min-height: 200px;
    margin: 0 auto;
    resize: none;
  }

  .TodoForm-buttonContainer {
    margin-top: 30px;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
}